@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap');

#canvas{
    border: 5px solid white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);   
    border-radius: 20px; 
    display: none;

}

#startGame{
    border: 5px solid white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);   
    border-radius: 20px;
    // background-image: url('../src/assets/bank2.jpg')

}

#endGame{
    border: 5px solid white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);   
    border-radius: 20px;
    display: none;
 
}

h1{
    // color: #efefcb;
    color: #eeeeb7;
    font-size: 78px;
    text-align: center;
    text-shadow: 3px 3px #3ab9dc;
}

body{
    font-family: 'Amatic SC', cursive;
    background-image: url("../src/assets/background5.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 100%;
}

.game-section{
    display: flex;
}

h4{
    font-size: 32px;
    text-shadow: 1px 1px #efefcb;
}

ul{
    list-style: none;
    margin-top: 150px;
    margin-left: 50px;
}

ul li{
    font-size: 24px;
}

li img{
    width: 60px;
}

// .bank{
//     right: 0;
//     height: 400px;
//     width: auto;
//     bottom: 0;
// }

.controllers, 
.play{
    font-size: 24px;
    display: none;
    font-weight: bold;
    background-color: white;
    opacity: 70%;
    border-radius: 5%;
    text-align: center;
    top: 50%;
    left: 50%;
    z-index: 2;
    width: 300px;
    height: 280px;
}

footer{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-decoration: none;
}

.social-links{
    display: flex;
    justify-content: center;
}

.links{
    padding-left: 15px;
    padding-right: 15px;
    filter: invert(20%);
}


#controllers:hover{
    .controllers{
        display: block;
    }
}

#directions:hover{
    .play{
        display: block;
    }
}

.social img:hover{
    opacity: 50%;
}

@media only screen and (max-width: 1720px) {
  .game-section{
      font-size: 24px;
      position: relative;
      display: inline-block;
      margin-top: 300px;

  }

  .info{
      display: flex;
      text-align: center;
      margin-top: 450px;
  }
}